import React from 'react';
import { PracticeMode } from '../hooks/usePracticeCommons';
import {
    // basicPracticeMenuItems, // kanaPracticeMenuItems で代替するため不要に
    stepUpPracticeMenuItems,
    alphabetAndNumberPracticeMenuItems,
    challengeMenuItems
} from '../data/keymapData';

interface PracticeMenuProps {
  practice: PracticeMode | ''; // currentPractice と PracticeMode の整合性のため PracticeMode | '' に変更
  handlePracticeSelect: (item: PracticeMode) => void;
  isAlphaNumericMode?: boolean;
}

const MenuItem: React.FC<{ item: PracticeMode; currentPractice: PracticeMode | ''; onClick: () => void }> = ({ item, currentPractice, onClick }) => (
    <div
        className={`cursor-pointer p-2 border-b text-sm hover:bg-gray-200 dark:hover:bg-gray-700 ${item === currentPractice ? 'bg-blue-100 dark:bg-blue-800 text-blue-700 dark:text-blue-300 font-semibold' : 'bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100'}`}
        onClick={onClick}
    >
        {item}
    </div>
);

// かな練習メニューに表示する項目を定義 (基本練習のみ)
const kanaBasicPracticeMenuItems: PracticeMode[] = [
    '清音の基本練習',
    '拗音の基本練習',
    '濁音の基本練習',
    '半濁音の基本練習',
    '小文字(促音)の基本練習',
    '記号の基本練習１',
    '記号の基本練習２',
    '記号の基本練習３',
];

// 英数練習メニューに表示する項目を定義 (基本練習のみ)
const alphaNumericBasicPracticeMenuItems: PracticeMode[] = [
    '英字の基本練習',
    '数字の基本練習',
    'Caps Lock操作', // 英数モードの基本操作としてここに含める
    '大文字変換操作', // 英数モードの基本操作としてここに含める
    '英字記号の練習１', // 追加
    '英字記号の練習２', // 追加
];

// かな練習用のステップアップメニュー項目
const kanaStepUpPracticeMenuItems: PracticeMode[] = [
    '拗濁音の練習',
    '拗半濁音の練習',
    '拗音拡張',
    '外来語の発音補助',
];

// 英数練習用のチャレンジメニュー項目
const alphaNumericChallengeMenuItems: PracticeMode[] = [
    '英字入力１分間トレーニング',
    '英数短文入力３分間トレーニング',
];

const PracticeMenu: React.FC<PracticeMenuProps> = ({ practice, handlePracticeSelect, isAlphaNumericMode = false }) => {
  // console.log('[PracticeMenu] Rendering. isAlphaNumericMode:', isAlphaNumericMode);
  return (
    <div className="p-4 border-r border-gray-300 dark:border-gray-700 overflow-y-auto h-full">
            {isAlphaNumericMode ? (
                <>
                    <h2 className="text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200">英数練習メニュー</h2>
                    <ul className="space-y-1">
                        {alphaNumericBasicPracticeMenuItems.map((item) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white' // 選択されている場合のスタイル
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700' // 通常時のスタイル
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>

                    {/* 英数練習にはステップアップメニューは設けない、基本操作を基本練習に含める */}
                    {/* <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>英数ステップアップ</h2>
                    <div className="space-y-1">
                        {(['Caps Lock操作', '大文字変換操作'] as PracticeMode[]).map((item) => ( // この項目は基本練習に移動
                            <MenuItem
                                key={item}
                                item={item}
                                currentPractice={practice}
                                onClick={() => handlePracticeSelect(item)}
                            />
                        ))}
                    </div>
                    */}
                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>英数チャレンジ</h2>
                    <ul className="space-y-1">
                        {/* 英数練習モードのチャレンジメニュー */}
                        {alphaNumericChallengeMenuItems.map((item) => ( // ここで直接 JSX 要素を返す
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                </>
            ) : (
                <>
                    <h2 className="text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200">かな練習メニュー</h2>
                    <ul className="space-y-1">
                        {kanaBasicPracticeMenuItems.map((item) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white' // 選択されている場合のスタイル
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700' // 通常時のスタイル
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>

                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>かなステップアップ</h2>
                    <ul className="space-y-1">
                        {/* かな練習モードのステップアップメニュー */}
                        {kanaStepUpPracticeMenuItems.map((item) => (
                             <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>

                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>かなチャレンジ</h2>
                    <ul className="space-y-1">
                        {/* かな練習モードのチャレンジメニュー */}
                        {challengeMenuItems.map((item) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                </>
            )}
    </div>
  );
};

export default PracticeMenu;
