import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { HIDDevice, HIDInputReportEvent } from './types/hid'; // パス修正済み
import {
    sampleJson,
    functionKeyMaps,
    layerNames, // layerNames を再度インポート
    alphabetPracticeGroups, // alphabetPracticeGroups をインポート
    youonGyouChars, // youonGyouChars をインポート
    youonGyouList, // youonGyouList をインポート
    dakuonGyouChars, // dakuonGyouChars をインポート
    dakuonGyouList, // dakuonGyouList をインポート
    handakuonGyouChars, // handakuonGyouChars をインポート
    handakuonGyouList, // handakuonGyouList をインポート
    sokuonKomojiData, // sokuonKomojiData をインポート
    kigoPractice1Data, // kigoPracticeData1 -> kigoPractice1Data に修正 (TS提案)
    // kigoPracticeData2, // kigoPracticeData2 は一旦コメントアウト (正しい名前が不明なため)
    // kigoPracticeData3, // kigoPracticeData3 は一旦コメントアウト (正しい名前が不明なため)
    // numberPracticeData, // numberPracticeData は一旦コメントアウト (存在しない可能性)
    youdakuonPracticeData, // youondakuonPracticeData -> youdakuonPracticeData に修正 (TS提案)
    youhandakuonPracticeData, // youhandakuonPracticeData をインポート
    youonKakuchoPracticeData, // youonKakuchoPracticeData をインポート
    gairaigoPracticeData, // gairaigoPracticeData をインポート
    gyouChars, // gyouChars をインポート
    gyouList, // gyouList をインポート
} from './data/keymapData';

import {
    PracticeMode,
    PracticeInputInfo,
    PracticeStatus, // PracticeStatus をインポート
} from './hooks/usePracticeCommons';
import usePracticeManagement from './hooks/usePracticeManagement'; // デフォルトインポートに変更
import { useKeyboardInitializer } from './hooks/useKeyboardInitializer';
import { useHidDevice } from './hooks/useHidDevice';
// import { useDisplayLogic } from './hooks/useDisplayLogic'; // useDisplayLogic のインポートを削除

import PracticeView from './components/PracticeView';
import LayoutView from './components/LayoutView';
import GlobalControls from './components/GlobalControls';

const ERROR_HIGHLIGHT_DURATION = 500; // 定数として定義

const isChallengeMode = (mode: PracticeMode | ''): boolean => {
    return mode === 'かな入力１分間トレーニング' ||
        mode === '記号入力１分間トレーニング' ||
        mode === '短文入力３分間トレーニング' ||
        mode === '英字入力１分間トレーニング' || // isChallengeMode に英数練習のチャレンジも追加
        mode === '英数短文入力３分間トレーニング';
};

export default function App() {
    // console.log('[App.tsx] Component rendering/re-rendering START');
    const { layers, title, cols, kb, side, fw, sn, showTrainingButton } = useKeyboardInitializer();
    // console.log('[App.tsx useKeyboardInitializer] Initialized values:', { layers, title, cols, kb, side, fw, sn, showTrainingButton });

    const [showKeyLabels, setShowKeyLabels] = useState(true);
    const [training, setTraining] = useState(false);
    const [isAlphaNumericMode, setIsAlphaNumericMode] = useState(false);
    // const [practiceViewKeySuffix, setPracticeViewKeySuffix] = useState(0); // PracticeView の key を更新するためのサフィックスを削除
    const [forceRenderKey, setForceRenderKey] = useState(0);
    // console.log('[App.tsx useState] Initial state values:', { showKeyLabels, training, isAlphaNumericMode });

    const {
        practice, gIdx, dIdx, isRandomMode, activePractice, headingChars, // activePractice は usePracticeManagement から取得
        isChallengeFinished, handlePracticeSelect, toggleRandomMode, setPractice, setGIdx, setDIdx, setIsRandomMode, nextStage,
        isRandomModeAvailable, showChallengeKeys: pmShowChallengeKeys, toggleShowChallengeKeys: pmToggleShowChallengeKeys
    } = usePracticeManagement({ side, layers, kb, /* showKeyLabels, */ isAlphaNumericMode }); // showKeyLabels removed as it's managed internally by usePracticeManagement for challenges
    // console.log('[App.tsx usePracticeManagement] Initial values:', { practice, gIdx, dIdx, isRandomMode, activePractice: !!activePractice, headingChars, isChallengeFinished });

    const currentTarget = activePractice?.targetChar;
    const practiceStatus = activePractice?.status;
    const typedChar = undefined; // typedChar は usePracticeManagement から直接返されない想定

    const [lastInvalidKeyCode, setLastInvalidKeyCode] = useState<number | null>(null);
    const [lastInvalidLayerIndex, setLastInvalidLayerIndex] = useState<number | null>(null); // 追加
    const errorClearTimeoutRef = useRef<NodeJS.Timeout | null>(null); // invalidInputTimeoutRef -> errorClearTimeoutRef に変更
    const pressedKeysRef = useRef<Map<number, number>>(new Map());
    const lastInvalidInputTime = useRef<number>(0);

    const trainingRef = useRef(training);
    const practiceRef = useRef(practice);
    const activePracticeRef = useRef(activePractice);
    const gIdxRef = useRef(gIdx);
    // const isProcessingHidReportRef = useRef(false); // HIDレポート処理中フラグを一旦削除
    const justAdvancedByInputRef = useRef(false);
    const dIdxRef = useRef(dIdx);

        const activePracticeTargetLayerIndices = activePractice?.targetLayerIndices;
        // console.log(`[App.tsx activePracticeTargetLayerIndices outside memo]: ${JSON.stringify(activePracticeTargetLayerIndices)}`);
        const activePracticeTargetLayerIndex = activePractice?.targetLayerIndex;
        // console.log(`[App.tsx activePracticeTargetLayerIndex outside memo]: ${activePracticeTargetLayerIndex}`);

    const displayLayerIndices = useMemo(() => {
        console.log(`[App.tsx DEBUG displayLayerIndices CALC START] training: ${training}, practice: "${practice}", activePractice exists: ${!!activePractice}, isAlphaNumericMode: ${isAlphaNumericMode}`);
        console.log(`  [App.tsx DEBUG displayLayerIndices CALC] Deps: activePracticeTargetLayerIndices: ${JSON.stringify(activePracticeTargetLayerIndices)}, activePracticeTargetLayerIndex: ${activePracticeTargetLayerIndex}`);
        // activePractice の中身も確認
        // console.log(`  [App.tsx DEBUG displayLayerIndices CALC] Full activePractice:`, activePractice ? { status: activePractice.status, targetChar: activePractice.targetChar, targetLayerIndex: activePractice.targetLayerIndex, targetLayerIndices: activePractice.targetLayerIndices } : null);

        let resultIndices: number[];
        if (training) {
                if (activePracticeTargetLayerIndices && activePracticeTargetLayerIndices.length > 0) {
                    // console.log('[App.tsx displayLayerIndices] Using activePractice.targetLayerIndices:', activePracticeTargetLayerIndices);
                    resultIndices = activePracticeTargetLayerIndices;
            }
                else if (typeof activePracticeTargetLayerIndex === 'number' && (!activePracticeTargetLayerIndices || activePracticeTargetLayerIndices.length === 0)) {
                    // console.log(`[App.tsx displayLayerIndices] Using activePractice.targetLayerIndex (single layer forced): [${activePracticeTargetLayerIndex}]`);
                    resultIndices = [activePracticeTargetLayerIndex];
            }
            else if (practice === '記号の基本練習１') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for 記号の基本練習１');
                resultIndices = [6];
            }
            else if (practice === '記号の基本練習２') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for 記号の基本練習２');
                resultIndices = [7];
            }
            else if (practice === '記号の基本練習３') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for 記号の基本練習３');
                resultIndices = [8];
            }
            else if (practice === '小文字(促音)の基本練習') {
                 // console.log('[App.tsx displayLayerIndices] Using specific rule for 小文字(促音)の基本練習');
                 resultIndices = [2, 3];
            }
            else if (practice === '清音の基本練習') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for 清音の基本練習');
                resultIndices = [2, 3];
            }
            else if (practice === '数字の基本練習') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for 数字の基本練習');
                resultIndices = [0];
            }
            else if (practice === 'かな入力１分間トレーニング') {
                // console.log('[App.tsx displayLayerIndices] Using specific rule for かな入力１分間トレーニング');
                resultIndices = [2, 3];
            }
            else if (practice === '短文入力３分間トレーニング') { // 「かな入力１分間トレーニング」と同じロジックを適用
                if (isChallengeMode(practice) && pmShowChallengeKeys) {
                    resultIndices = [2, 3];
                } else {
                    resultIndices = [];
                }
            }
            else if (practice === '英数短文入力３分間トレーニング') {
                console.log(`[App.tsx DEBUG displayLayerIndices] Specific rule for "英数短文入力３分間トレーニング".`);
                if (activePracticeTargetLayerIndices && activePracticeTargetLayerIndices.length > 0) {
                    console.log(`  [App.tsx DEBUG displayLayerIndices] "英数短文入力３分間トレーニング" - Using activePractice.targetLayerIndices:`, activePracticeTargetLayerIndices);
                    resultIndices = activePracticeTargetLayerIndices;
                } else if (typeof activePracticeTargetLayerIndex === 'number') {
                    console.log(`  [App.tsx DEBUG displayLayerIndices] "英数短文入力３分間トレーニング" - Using activePractice.targetLayerIndex: [${activePracticeTargetLayerIndex}]`);
                    resultIndices = [activePracticeTargetLayerIndex];
                } else {
                    console.log('  [App.tsx DEBUG displayLayerIndices] "英数短文入力３分間トレーニング" - Fallback to [4, 5] because no layer info from activePractice.');
                    resultIndices = [4, 5]; // 元のフォールバック
                }
            }
            // Default based on mode if no specific practice rule matched and hook didn't provide layers
            else if (isAlphaNumericMode) {
                // This will catch modes like "英数短文入力３分間トレーニング", "英字入力１分間トレーニング"
                // if their hooks don't set targetLayerIndex/Indices.
                // console.log(`[App.tsx displayLayerIndices] AlphaNumeric mode ("${practice}") without specific layers from hook. Defaulting to [0, 1].`);
                resultIndices = [0, 1];
            }
            // Fallback for other cases (e.g. practice === '' or unhandled Kana modes)
            else if (practice === '') {
                // console.log('[App.tsx displayLayerIndices] Training ON, but no practice selected. Returning default Kana layers [2, 3].');
                resultIndices = [2, 3];
            } else {
                // console.log(`[App.tsx displayLayerIndices] Unhandled Kana practice ("${practice}"). Defaulting to Kana layers [2, 3].`);
                resultIndices = [2, 3];
            }
        }
        else {
            const defaultLayers = layers.length > 1 ? [0, 1] : [0];
            // console.log('[App.tsx displayLayerIndices] Training OFF, returning default layers:', defaultLayers);
            resultIndices = defaultLayers;
        }
        console.log(`[App.tsx DEBUG displayLayerIndices CALC END] Result:`, resultIndices);
        return resultIndices;
        }, [training, practice, activePracticeTargetLayerIndices, activePracticeTargetLayerIndex, layers, isAlphaNumericMode, activePractice]); // activePractice を依存配列に追加

    const handleInvalidInput = useCallback((pressCode: number, layerIndex: number) => {
        const now = Date.now();
        const originalPressCode = pressCode; // タイムアウト内で使うため保持
        if (now - lastInvalidInputTime.current < 50) {
            // console.log(`[App.tsx handleInvalidInput] Debounced. pressCode: ${pressCode}, layerIndex: ${layerIndex}`);
            return;
        }
        lastInvalidInputTime.current = now;
        console.log(`[App.tsx handleInvalidInput] Invalid input. pressCode: ${pressCode} (0-based), layerIndex: ${layerIndex}`);
        setLastInvalidKeyCode(pressCode + 1); // UI表示用に +1
        setLastInvalidLayerIndex(layerIndex); // lastInvalidLayerIndex をセット

        if (errorClearTimeoutRef.current !== null) {
            clearTimeout(errorClearTimeoutRef.current);
        }
        errorClearTimeoutRef.current = setTimeout(() => {
            // console.log(`[App.tsx handleInvalidInput TIMEOUT] Clearing invalid highlight for pressCode: ${originalPressCode + 1}`);
            setLastInvalidKeyCode(prevCode => {
                if (prevCode === (originalPressCode + 1)) {
                    setLastInvalidLayerIndex(null);
                    return null;
                }
                return prevCode;
            });
            // console.log(`[App.tsx handleInvalidInput TIMEOUT] activePracticeRef.current?.errorHighlightLayer BEFORE clearErrorState: ${activePracticeRef.current?.errorHighlightLayer}`);
            const practiceHook = activePracticeRef.current; // タイムアウト実行時の ref の値を保持
            if (practiceHook && typeof practiceHook.clearErrorState === 'function') {
                practiceHook.clearErrorState();
            }
            // console.log(`[App.tsx handleInvalidInput TIMEOUT] practiceHook?.errorHighlightLayer AFTER clearErrorState (hook internal state should be null): ${practiceHook?.errorHighlightLayer}`);
            // setForceRenderKey(prev => prev + 1);
            errorClearTimeoutRef.current = null;
        }, ERROR_HIGHLIGHT_DURATION);
    }, []); // 依存配列は空のまま

    const handleHidInputReport = useCallback((ev: HIDInputReportEvent) => { // displayLayerIndices を依存配列に追加
        // if (isProcessingHidReportRef.current) {
        //     console.warn("[App.tsx handleHidInputReport] Skipped due to isProcessingHidReportRef being true.");
        //     return;
        // }
        // isProcessingHidReportRef.current = true;

        const data = new Uint8Array(ev.data.buffer); // displayLayerIndices を依存配列に追加
        const reportId = data[0];
        const keyCode = data[1];
        console.log(`[App.tsx handleHidInputReport START] Raw HID Data: reportId=${reportId}, keyCode=0x${keyCode.toString(16)} (${keyCode})`);
        const timestamp = Date.now();

        if (reportId === 0x14 && keyCode === 0x03) { // Keep-alive packet from QMK firmware
            console.log('[App.tsx handleHidInputReport] Ignoring keep-alive report (0x14, 0x03).');
            // isProcessingHidReportRef.current = false;
            return;
        }

        if (reportId === 0x15 && trainingRef.current && practiceRef.current && activePracticeRef.current?.handleInput) { // ref を使用
            console.log('[App.tsx handleHidInputReport] Processing reportId 0x15 for training.');
            const releaseOffset = 0x14;
            const maxStartLayoutKeyCode = 0x14;
            const isPressEventAdjusted = keyCode <= maxStartLayoutKeyCode;
            const isReleaseEventAdjusted = keyCode >= (releaseOffset + 1);

            console.log(`[App.tsx handleHidInputReport] Event Type Check: keyCode=0x${keyCode.toString(16)}, isPressEventAdjusted=${isPressEventAdjusted}, isReleaseEventAdjusted=${isReleaseEventAdjusted}`);

            if (isPressEventAdjusted) {
                console.log('[App.tsx handleHidInputReport] Event identified as PRESS.');
                // press イベントでも keyCode を0ベースのインデックスに変換する
                const oneBasedPressCode = keyCode; // デバイスからのコードは1ベース
                const zeroBasedPressCode = oneBasedPressCode - 1; // 0ベースに変換
                console.log(`[App.tsx handleHidInputReport] PRESS event. oneBasedPressCode: ${oneBasedPressCode}, zeroBasedPressCode: ${zeroBasedPressCode}, timestamp: ${timestamp}`);
                if (zeroBasedPressCode < 0) {
                    // isProcessingHidReportRef.current = false;
                    console.log('[App.tsx handleHidInputReport] Invalid zeroBasedPressCode (<0) for PRESS event. Ignoring.');
                    return;
                }
                pressedKeysRef.current.set(oneBasedPressCode, timestamp); // pressedKeysRef には1ベースのコードを保存（リリース時との突き合わせのため）
                
                // Determine the layerIndex for PracticeInputInfo
                let layerForInputInfoPress: number;
                const practiceHookTargetLayerPress = activePracticeRef.current?.targetLayerIndex;

                if (typeof practiceHookTargetLayerPress === 'number') {
                    // If the practice hook specifies a target layer, prefer that.
                    layerForInputInfoPress = practiceHookTargetLayerPress;
                } else {
                    // Fallback to the primary displayed layer if the hook doesn't specify one.
                    layerForInputInfoPress = displayLayerIndices[0] ?? 0;
                }
                console.log(`[App.tsx handleHidInputReport PRESS] Determined layerForInputInfo: ${layerForInputInfoPress}. practiceHookTargetLayer: ${practiceHookTargetLayerPress}, displayLayerIndices[0]: ${displayLayerIndices[0]}`);

                const inputInfo: PracticeInputInfo = {
                    type: 'press',
                    timestamp,
                    pressCode: zeroBasedPressCode,
                    layerIndex: layerForInputInfoPress 
                };
                // Null check for activePracticeRef.current.handleInput
                if (activePracticeRef.current && typeof activePracticeRef.current.handleInput === 'function') {
                    activePracticeRef.current.handleInput(inputInfo);
                }
            } else if (isReleaseEventAdjusted) {
                console.log('[App.tsx handleHidInputReport] Event identified as RELEASE.');
                const pressCode = keyCode - releaseOffset;
                // console.log(`[App.tsx handleHidInputReport] pressCodeFromDevice (after offset 0x${releaseOffset.toString(16)}): 0x${pressCode.toString(16)} (${pressCode})`);
                if (pressCode <= 0) {
                    // isProcessingHidReportRef.current = false;
                    return;
                }

                const pressTimestamp = pressedKeysRef.current.get(pressCode);
                if (pressTimestamp) {
                    pressedKeysRef.current.delete(pressCode);
                    console.log(`[App.tsx handleHidInputReport] RELEASE event detected. Raw pressCode from device (after offset): ${pressCode}, Timestamp: ${timestamp}`);
                    const zeroBasedPressCode = pressCode - 1;
                    // console.log(`[App.tsx handleHidInputReport] zeroBasedPressCode (for practice hook): 0x${zeroBasedPressCode.toString(16)} (${zeroBasedPressCode})`);
                    // このコメントアウトされた行は、以前の修正の残骸であり、現在は不要です。
                    if (zeroBasedPressCode < 0) {
                        console.log('[App.tsx handleHidInputReport] Invalid zeroBasedPressCode (<0). Ignoring.');
                        return; // 無効なコードは無視
                    }

                    // Determine the layerIndex for PracticeInputInfo on RELEASE
                    let layerForInputInfoRelease: number;
                    const practiceHookTargetLayerRelease = activePracticeRef.current?.targetLayerIndex;

                    if (typeof practiceHookTargetLayerRelease === 'number') {
                        layerForInputInfoRelease = practiceHookTargetLayerRelease;
                    } else {
                        layerForInputInfoRelease = displayLayerIndices[0] ?? 0;
                    }
                    console.log(`[App.tsx handleHidInputReport RELEASE] Determined layerForInputInfo: ${layerForInputInfoRelease}. practiceHookTargetLayer: ${practiceHookTargetLayerRelease}, displayLayerIndices[0]: ${displayLayerIndices[0]}`);

                    const inputInfoRelease: PracticeInputInfo = {
                        type: 'release',
                        timestamp,
                        pressCode: zeroBasedPressCode,
                        layerIndex: layerForInputInfoRelease
                    };

                    console.log(`[App.tsx handleHidInputReport] PREPARING TO CALL activePractice.handleInput for RELEASE. inputInfo:`, JSON.stringify(inputInfoRelease));
                    // Null check for activePracticeRef.current.handleInput
                    if (!activePracticeRef.current || typeof activePracticeRef.current.handleInput !== 'function') {
                        console.warn('[App.tsx handleHidInputReport] activePractice.current.handleInput is not available for RELEASE.');
                        return;
                    }
                    const result = activePracticeRef.current.handleInput(inputInfoRelease);
                    console.log(`[App.tsx HID_INPUT_RESULT for ${practiceRef.current}] isExpected: ${result?.isExpected}, shouldGoToNext: ${result?.shouldGoToNext}, dIdx (hook internal, if available): ${activePracticeRef.current?.dIdx}, App's dIdx: ${dIdxRef.current}, App's gIdx: ${gIdxRef.current}`);
    
                    let appDidDriveAdvancement = false;
                    if (result) {
                        // shouldGoToNext が true の場合は、練習項目を次に進める (gIdx または dIdx)
                        if (result.shouldGoToNext === true) {
                            console.log(`[App.tsx HID_INPUT_ADVANCE for ${practiceRef.current}] shouldGoToNext is TRUE. Calling nextStage.`);
                            setLastInvalidKeyCode(null);
                            setLastInvalidLayerIndex(null);
                            if (errorClearTimeoutRef.current !== null) {
                                clearTimeout(errorClearTimeoutRef.current);
                                errorClearTimeoutRef.current = null;
                            }
                            justAdvancedByInputRef.current = true;
                            appDidDriveAdvancement = true;
                            nextStage(practiceRef.current);
                        } else if (result.shouldGoToNext === false) { // shouldGoToNext が明示的に FALSE の場合
                            if (result.isExpected) {
                                console.log(`[App.tsx HID_INPUT_STAY/ADVANCE_DIDX for ${practiceRef.current}] shouldGoToNext is FALSE, isExpected is TRUE.`);
                                setLastInvalidKeyCode(null);
                                setLastInvalidLayerIndex(null);
                                if (errorClearTimeoutRef.current !== null) {
                                    clearTimeout(errorClearTimeoutRef.current);
                                    errorClearTimeoutRef.current = null;
                                }

                                const practicesNotAdvancingDIdxOnFalse: PracticeMode[] = [
                                    // Basic Kana practices with gyou+dan input
                                    '清音の基本練習',
                                    '濁音の基本練習',
                                    '半濁音の基本練習',
                                    '拗音の基本練習',
                                    '拗濁音の練習',
                                    '拗半濁音の練習',
                                    '小文字(促音)の基本練習',
                                    // Practices with multi-step or self-managed progression
                                    '英字の基本練習', // e.g. abc -> a (group key is expected but doesn't advance dIdx)
                                    '数字の基本練習',
                                    'Caps Lock操作', // Ensure Caps Lock practice doesn't advance dIdx here
                                    '大文字変換操作', // Add CorrectionPractice to prevent dIdx advancement on group key
                                    '英字記号の練習１', // Add AlphaSymbolPractice1 to prevent dIdx advancement on "記号" key
                                    '英字記号の練習２', // Add AlphaSymbolPractice1 to prevent dIdx advancement on "記号" key
                                    '外来語の発音補助',
                                    '拗音拡張',
                                    // Challenge modes that manage their own char-by-char progression
                                    'かな入力１分間トレーニング',
                                    '記号入力１分間トレーニング',
                                    '短文入力３分間トレーニング',
                                    '英字入力１分間トレーニング',
                                    '英数短文入力３分間トレーニング',
                                ];

                                if (practicesNotAdvancingDIdxOnFalse.includes(practiceRef.current as PracticeMode)) {
                                    console.log(`  [App.tsx] Practice "${practiceRef.current}" is in practicesNotAdvancingDIdxOnFalse. Not advancing dIdx.`);
                                    justAdvancedByInputRef.current = false; 
                                    // appDidDriveAdvancement remains false
                                } else {
                                    console.log(`  [App.tsx] Practice "${practiceRef.current}" NOT in practicesNotAdvancingDIdxOnFalse. Advancing dIdx.`);
                                    justAdvancedByInputRef.current = true;
                                    appDidDriveAdvancement = true;
                                    setDIdx(prevDIdx => prevDIdx + 1);
                                }
                            } else {
                                // shouldGoToNext is FALSE, isExpected is FALSE -> これは現在のステージ/練習項目にとって予期しない入力です。
                                console.log(`[App.tsx HID_INPUT_UNEXPECTED for ${practiceRef.current}] shouldGoToNext is FALSE, isExpected is FALSE.`);
                                // Caps Lock操作練習におけるCaps Lockキー入力は、予期されるが進行しない入力なので、エラー表示はしない (inputInfoRelease を使用)
                                if (!(practiceRef.current === 'Caps Lock操作' && inputInfoRelease.layerIndex === 4 && inputInfoRelease.pressCode === 1)) {
                                    let layerToHighlightOnError = result.errorExpectedLayer ?? result.errorLayer ?? activePracticeRef.current?.targetLayerIndex ?? 2;
                                    handleInvalidInput(zeroBasedPressCode, layerToHighlightOnError);
                                }
                                justAdvancedByInputRef.current = false;
                            }
                        } else if (result.shouldGoToNext === undefined) {
                            if (result.isExpected) {
                                console.log(`[App.tsx HID_INPUT_SELF_MANAGE/NO_ADVANCE for ${practiceRef.current}] shouldGoToNext is UNDEFINED, isExpected is TRUE.`);
                                setLastInvalidKeyCode(null);
                                setLastInvalidLayerIndex(null);
                                if (errorClearTimeoutRef.current !== null) {
                                    clearTimeout(errorClearTimeoutRef.current);
                                    errorClearTimeoutRef.current = null;
                                }
                                justAdvancedByInputRef.current = false;
                            } else {
                                // shouldGoToNext が UNDEFINED, isExpected が FALSE -> 自己管理型練習にとって予期しない入力です。
                                console.log(`[App.tsx HID_INPUT_UNEXPECTED for ${practiceRef.current}] shouldGoToNext is UNDEFINED, isExpected is FALSE.`);
                                // Caps Lock操作練習におけるCaps Lockキー入力は、予期されるが進行しない入力なので、エラー表示はしない (inputInfoRelease を使用)
                                if (!(practiceRef.current === 'Caps Lock操作' && inputInfoRelease.layerIndex === 4 && inputInfoRelease.pressCode === 1)) {
                                    let layerToHighlightOnError = result.errorExpectedLayer ?? result.errorLayer ?? activePracticeRef.current?.targetLayerIndex ?? 2;
                                    handleInvalidInput(zeroBasedPressCode, layerToHighlightOnError);
                                }
                                justAdvancedByInputRef.current = false;
                            }
                        } else if (!result.isExpected) { // Fallback for other cases where isExpected is false
                            console.log(`[App.tsx HID_INPUT_ERROR (fallback) for ${practiceRef.current}] isExpected is FALSE.`);
                            // Caps Lock操作練習におけるCaps Lockキー入力は、予期されるが進行しない入力なので、エラー表示はしない (inputInfoRelease を使用)
                            if (!(practiceRef.current === 'Caps Lock操作' && inputInfoRelease.layerIndex === 4 && inputInfoRelease.pressCode === 1)) {
                                let layerToHighlightOnError = result.errorExpectedLayer ?? result.errorLayer ?? activePracticeRef.current?.targetLayerIndex ?? 2;
                                handleInvalidInput(zeroBasedPressCode, layerToHighlightOnError);
                            }
                            justAdvancedByInputRef.current = false;
                        }

                        if (appDidDriveAdvancement) {
                            setTimeout(() => {
                                justAdvancedByInputRef.current = false;
                            }, 0);
                        }
                    } else {
                        console.warn('[App.tsx handleHidInputReport] Input result was falsy or activePractice is missing. No action taken.');
                        justAdvancedByInputRef.current = false;
                    }
                } else {
                    console.log(`[App.tsx handleHidInputReport] No pressTimestamp found for pressCode: ${pressCode}. Ignoring release event.`);
                    justAdvancedByInputRef.current = false; // プレス情報がない場合もフラグをリセット
                }
            } else {
                console.log(`[App.tsx handleHidInputReport] Unhandled keyCode: 0x${keyCode.toString(16)} for reportId 0x15.`);
                justAdvancedByInputRef.current = false; // 未処理の場合もフラグをリセット
            }
        } else {
            if (reportId === 0x15) {
                console.log('[App.tsx handleHidInputReport] ReportId 0x15 received, but not in training or practice or activePractice.handleInput is missing.');
            }
            justAdvancedByInputRef.current = false; // 練習外の入力でもフラグをリセット
        }
        // isProcessingHidReportRef.current = false; // 処理の最後にフラグを解除
    }, [handleInvalidInput, nextStage, setDIdx, displayLayerIndices]); // displayLayerIndices を依存配列に追加

    const { device: hidDevice, isConnected: isHidConnected, requestDevice: requestHidDevice, sendReport: sendHidReport, error: hidError } = useHidDevice({ onInputReport: handleHidInputReport, autoConnect: true });

    useEffect(() => {
        // console.log('[App.tsx useEffect isHidConnected] HID connection status changed:', isHidConnected);
        if (hidError) {
            console.error('[App.tsx useEffect isHidConnected] HID Error:', hidError);
        }
    }, [isHidConnected, hidError]);

    const sendHidReportForTraining = useCallback(async (on: boolean) => {
        // console.log(`[App.tsx sendHidReportForTraining] Called with on: ${on}. Current isHidConnected: ${isHidConnected}`);
        if (!isHidConnected && !on) {
            // console.log('[App.tsx sendHidReportForTraining] Not connected and turning off. Resetting state.');
            setTraining(false);
            setPractice(''); setGIdx(0); setDIdx(0);
            setLastInvalidKeyCode(null);
            setLastInvalidLayerIndex(null); // エラーレイヤーもクリア
            if (errorClearTimeoutRef.current !== null) { // invalidInputTimeoutRef -> errorClearTimeoutRef
                clearTimeout(errorClearTimeoutRef.current);
                errorClearTimeoutRef.current = null;
            }
            if (isAlphaNumericMode) {
                setIsAlphaNumericMode(false);
            }
            pressedKeysRef.current.clear(); activePracticeRef.current?.reset?.(); // ref を使用
            setShowKeyLabels(true); setIsRandomMode(false);
            return;
        }

        let currentDevice = hidDevice;
        if (!currentDevice || !currentDevice.opened) {
            // console.log('[App.tsx sendHidReportForTraining] HID device not available or not opened. Requesting device.');
            try {
                currentDevice = await requestHidDevice();
                if (!currentDevice) {
                    console.error("[App.tsx sendHidReportForTraining] HID device connection failed. Cannot start training.");
                    if (on) setTraining(false);
                    return;
                }
                // console.log('[App.tsx sendHidReportForTraining] HID device connected successfully.');
            } catch (err) {
                console.error("[App.tsx sendHidReportForTraining] Error during HID device connection:", err);
                if (on) setTraining(false);
                return;
            }
        }

        try {
            // console.log(`[App.tsx sendHidReportForTraining] Sending HID report: 0x02, ${on ? 0x01 : 0x00}`);
            await sendHidReport(0, new Uint8Array([0x02, on ? 0x01 : 0x00]));
            // console.log('[App.tsx sendHidReportForTraining] HID report sent successfully.');
        } catch (error) {
            console.error("[App.tsx sendHidReportForTraining] HID report sending error:", error);
            if (on) setTraining(false); // 送信失敗時にも training を false に戻す
            return;
        }

        setTraining(on);
        // console.log(`[App.tsx sendHidReportForTraining] setTraining(${on})`);
        if (on) {
            // console.log('[App.tsx sendHidReportForTraining] Training turned ON. Setting defaults.');
            setShowKeyLabels(true);
            setIsRandomMode(false);
            if (!practiceRef.current && !isAlphaNumericMode) { // ref を使用
                // console.log('[App.tsx sendHidReportForTraining] No practice selected and not in alphaNumericMode.');
            }
        } else {
            // console.log('[App.tsx sendHidReportForTraining] Training turned OFF. Resetting practice state.');
            setPractice('');
            setGIdx(0); setDIdx(0);
            setLastInvalidKeyCode(null);
            setLastInvalidLayerIndex(null); // エラーレイヤーもクリア
            if (errorClearTimeoutRef.current !== null) { // invalidInputTimeoutRef -> errorClearTimeoutRef
                clearTimeout(errorClearTimeoutRef.current);
                errorClearTimeoutRef.current = null;
            }
            pressedKeysRef.current.clear();
            if (isAlphaNumericMode) {
                setIsAlphaNumericMode(false);
            }
            activePracticeRef.current?.reset?.(); // ref を使用
            setShowKeyLabels(true);
            setIsRandomMode(false);
        }
     }, [
        isHidConnected,
        hidDevice,
        requestHidDevice,
        sendHidReport,
        isAlphaNumericMode,
        setPractice,
        setGIdx,
        setDIdx,
        setIsAlphaNumericMode,
        setIsRandomMode,
        setShowKeyLabels,
        setTraining,
        errorClearTimeoutRef,
        pressedKeysRef,
        activePracticeRef,
        practiceRef,
        setLastInvalidKeyCode,
        setLastInvalidLayerIndex
    ]);

    useEffect(() => {
        // console.log(`[App.tsx useEffect training] training changed: ${training}`);
        trainingRef.current = training;
    }, [training]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect practice] practice changed: "${practice}"`);
        practiceRef.current = practice;
    }, [practice]);
    useEffect(() => {
        console.log(`[App.tsx DEBUG useEffect activePractice] activePractice changed. Exists: ${!!activePractice}`);
        activePracticeRef.current = activePractice;
        if (activePractice) {
            console.log(`  [App.tsx DEBUG activePractice details] Status: ${activePractice.status}, Target: ${activePractice.targetChar}, TargetLayerIndex: ${activePractice.targetLayerIndex}, TargetLayerIndices: ${JSON.stringify(activePractice.targetLayerIndices)}, DisplayLayers: ${JSON.stringify(activePractice.displayLayers)}, Has getHighlightClassName: ${typeof activePractice.getHighlightClassName === 'function'}`);
        }
    }, [activePractice]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect gIdx] gIdx changed: ${gIdx}`);
        gIdxRef.current = gIdx;
    }, [gIdx]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect dIdx] dIdx changed: ${dIdx}`);
        dIdxRef.current = dIdx;
    }, [dIdx]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect isAlphaNumericMode] isAlphaNumericMode changed: ${isAlphaNumericMode}`);
    }, [isAlphaNumericMode]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect showKeyLabels] showKeyLabels changed: ${showKeyLabels}`);
    }, [showKeyLabels]);
    useEffect(() => {
        // console.log(`[App.tsx useEffect isRandomMode] isRandomMode changed: ${isRandomMode}`);
    }, [isRandomMode]);

    // activePractice の変更を監視し、必要に応じて nextStage を呼び出す useEffect (問題の可能性箇所)
    useEffect(() => {
        console.log(`[App.tsx useEffect for activePractice advancement] Triggered. justAdvancedByInputRef: ${justAdvancedByInputRef.current}, activePractice status: ${activePracticeRef.current?.status}`);
        if (justAdvancedByInputRef.current && practiceRef.current) { // practiceRef.current is enough; nextStage is always a function here
            console.log('[App.tsx useEffect for activePractice advancement] Advancement was (likely) just handled by input, resetting flag and skipping this effect logic.');
            // Do NOT reset the flag here. Let the setTimeout in handleHidInputReport do it.
            return; // Skip any advancement logic in this useEffect if input just happened.
        }

        // この useEffect が nextStage を呼び出す具体的な条件は、
        // 既存のロジックや、なぜここから nextStage が呼ばれるのかという設計意図に依存します。
        // 例えば、特定の練習モードで、特定のステータスになった場合に自動で次に進む、など。
        // 今回のダブルアドバンスメント問題の直接的な原因となっている場合、 (このコメントは今回のエラーとは直接関係なさそう)
        // この useEffect からの nextStage 呼び出し自体が不要であるか、
        // より厳しい条件付けが必要になる可能性があります。
        // ここでは、フラグによる制御のみを追加し、既存の呼び出しロジックはコメントアウトなどで一旦無効化することも検討してください。

    }, [activePractice, gIdx, dIdx, training, practice, nextStage, justAdvancedByInputRef, activePracticeRef, practiceRef]);

    // isInvalidInputTarget のコールバックを定義
    const isInvalidInputTargetCallback = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        // Log only when there's an active invalid key state to reduce console noise
        // ログを削減するため、無効なキー入力状態がアクティブな場合のみログ出力する
        if (lastInvalidKeyCode !== null || lastInvalidLayerIndex !== null) {
            // console.log(
            //     `[App.tsx isInvalidInputTarget CALLED] Args: targetPressCode=${targetPressCode}, currentLayoutIndex=${currentLayoutIndex}, currentKeyIndex=${currentKeyIndex}. App State: lastInvalidKeyCode=${lastInvalidKeyCode}, lastInvalidLayerIndex=${lastInvalidLayerIndex}`
            // );
        }
        return currentLayoutIndex === lastInvalidLayerIndex && currentKeyIndex === (lastInvalidKeyCode !== null ? lastInvalidKeyCode -1 : -1);
    }, [lastInvalidKeyCode, lastInvalidLayerIndex]);

    const keyWidthRem = 5.5;
    const fixedWidthNum = cols * keyWidthRem;
    const fixedWidth = `${fixedWidthNum}rem`;

    const currentFunctionKeyMap = useMemo(() => {
        const map = functionKeyMaps[kb as keyof typeof functionKeyMaps]?.[side as keyof typeof functionKeyMaps[keyof typeof functionKeyMaps]] ?? {};
        // console.log('[App.tsx useMemo currentFunctionKeyMap] Calculated:', { kb, side, map });
        return map;
    }, [kb, side]);

    const buttonStyle: React.CSSProperties = {
        marginBottom: '0.5rem',
        padding: '5px 10px',
        display: 'block',
        minWidth: '120px',
        textAlign: 'center',
     };

        const activePracticeDisplayLayers = activePractice?.displayLayers;

    const keyboardLayers = useMemo(() => {
        // console.log(`[App.tsx keyboardLayers CALC START] practice: "${practice}", activePractice exists: ${!!activePractice}, kb: ${kb}, side: ${side}`);
        const baseLayers = sampleJson[kb]?.[side]?.layers ?? [];
        // activePractice.displayLayers が存在すれば、それを優先的に使用する
            if (activePracticeDisplayLayers) {
            // console.log(`[App.tsx keyboardLayers] Using activePractice.displayLayers for practice "${practice}":`, activePracticeDisplayLayers);
                return activePracticeDisplayLayers;
        }
        // console.log('[App.tsx keyboardLayers] Using baseLayers:', baseLayers);
        return baseLayers;
        }, [practice, activePracticeDisplayLayers, kb, side]); // activePractice を activePracticeDisplayLayers に変更
    // console.log('[App.tsx] PRE-RENDER LOGS END. About to return JSX.');
    return (
        <>{/* Outermost fragment start */}
            {/* {console.log("[App.tsx] Before rendering PracticeView (inside JSX)", { practice, isAlphaNumericMode, headingChars, activePractice, "activePractice?.headingChars": activePractice?.headingChars, "activePractice?.status": activePractice?.status })} */}
            {/* {console.log(`[App.tsx PRE-PracticeView RENDER] practice: "${practice}", training: ${training}, isAlphaNumericMode: ${isAlphaNumericMode}, headingChars to pass: ${JSON.stringify(headingChars)}, activePractice?.headingChars: ${JSON.stringify(activePractice?.headingChars)}`)} */}
            <div className='p-4 pt-20'> {/* Main content div */}
            {/* PracticeView レンダリング直前のログ */}
            {training &&
              (() => {
                console.log(`[App.tsx DEBUG RENDER PracticeView] practice: "${practice}", training: ${training}, isAlphaNumericMode: ${isAlphaNumericMode}`);
                console.log(`  [App.tsx DEBUG RENDER PracticeView] displayLayerIndices: ${JSON.stringify(displayLayerIndices)}`);
                console.log(`  [App.tsx DEBUG RENDER PracticeView] keyboardLayers length: ${keyboardLayers?.length}`);
                console.log(`  [App.tsx DEBUG RENDER PracticeView] activePractice exists: ${!!activePractice}`);
                if (activePractice) {
                  console.log(`    [App.tsx DEBUG RENDER PracticeView] activePractice.status: ${activePractice.status}, activePractice.targetChar: ${activePractice.targetChar}, has getHighlightClassName: ${typeof activePractice.getHighlightClassName === 'function'}`);
                }
                console.log(`  [App.tsx DEBUG RENDER PracticeView] lastInvalidKeyCode: ${lastInvalidKeyCode}, lastInvalidLayerIndex: ${lastInvalidLayerIndex}`);
                return null; // Ensure this block results in a single 'null' child for React
              })()}
            <GlobalControls
                key={`global-controls-${practice}`} // Add key to ensure re-render when practice changes, for button text
                training={training}
                showTrainingButton={showTrainingButton} // showKeyLabels prop removed
                isRandomMode={isRandomMode}
                isRandomModeAvailable={isRandomModeAvailable} // GlobalControls に isRandomModeAvailable を渡す
                practice={practice}
                onToggleTraining={() => {
                    // console.log(`[App.tsx GlobalControls onToggleTraining] Current practice: "${practice}"`);
                    // console.log(`[App.tsx GlobalControls onToggleTraining] pmShowChallengeKeys: ${pmShowChallengeKeys}`);
                    // console.log('[App.tsx GlobalControls onToggleTraining] Clicked.');
                    if (training && isAlphaNumericMode) {
                        // console.log('[App.tsx GlobalControls onToggleTraining] Was training and in alphaNumericMode, setting isAlphaNumericMode to false.');
                        setIsAlphaNumericMode(false);
                    }
                    const newTrainingState = !training;
                    // console.log(`[App.tsx GlobalControls onToggleTraining] New training state will be: ${newTrainingState}`);
                    sendHidReportForTraining(newTrainingState);
                    if (newTrainingState) { // かな練習を始める場合
                        // console.log('[App.tsx GlobalControls onToggleTraining] Starting Kana practice, ensuring isAlphaNumericMode is false.');
                        setIsAlphaNumericMode(false); // 明示的にかなモードに
                    }
                }}
                onStartAlphaNumericPractice={() => {
                    // console.log('[App.tsx GlobalControls onStartAlphaNumericPractice] Clicked.');
                    if (!training) {
                        // console.log('[App.tsx GlobalControls onStartAlphaNumericPractice] Training was OFF. Starting training for alphanumeric.');
                        sendHidReportForTraining(true).then(() => {
                            // console.log('[App.tsx GlobalControls onStartAlphaNumericPractice] sendHidReportForTraining(true) successful. Setting isAlphaNumericMode to true.');
                            setIsAlphaNumericMode(true);
                        }).catch(err => {
                            console.error("Failed to start training for alphanumeric practice:", err);
                        });
                    } else {
                        // console.log('[App.tsx GlobalControls onStartAlphaNumericPractice] Training was ON. Setting isAlphaNumericMode to true.');
                        setIsAlphaNumericMode(true);
                    }
                }}
                onToggleKeyLabels={
                    isChallengeMode(practice) ? pmToggleShowChallengeKeys : () => setShowKeyLabels(prev => !prev)
                }
                actualShowKeyLabelsState={isChallengeMode(practice) ? pmShowChallengeKeys : showKeyLabels}
                onToggleRandomMode={() => {
                    // console.log('[App.tsx GlobalControls onToggleRandomMode] Clicked.');
                    toggleRandomMode();
                }}
                buttonStyle={buttonStyle}
            />

            {training ? (
                <>{/* Inner fragment for training true */}
                {/* {console.log(`[App.tsx Render] Training is ON. isAlphaNumericMode: ${isAlphaNumericMode}. Rendering PracticeView.`)} */}                {isAlphaNumericMode ? (
                        <PracticeView
                            key={`practice-view-alpha-${practice}-${isAlphaNumericMode}-${pmShowChallengeKeys}`}
                            practice={practice}
                            handlePracticeSelect={handlePracticeSelect}
                            gIdx={gIdx}
                            dIdx={dIdx}
                            headingChars={headingChars}
                            isChallengeFinished={isChallengeFinished}
                            activePractice={activePractice}
                            isRandomMode={isChallengeMode(practice) ? true : isRandomMode}
                            displayLayerIndices={displayLayerIndices}
                            keyboardLayers={keyboardLayers}
                            cols={cols}
                            fixedWidth={fixedWidth}
                            showKeyLabels={isChallengeMode(practice) ? pmShowChallengeKeys : showKeyLabels}
                            lastInvalidKeyCode={lastInvalidKeyCode}
                            lastInvalidLayerIndex={lastInvalidLayerIndex}
                            currentFunctionKeyMap={currentFunctionKeyMap}
                            training={training}
                            typedChar={typedChar}
                            isAlphaNumericMode={true}
                            isInvalidInputTarget={isInvalidInputTargetCallback}
                        />
                    ) : (
                        <PracticeView
                            key={`practice-view-kana-${practice}-${isAlphaNumericMode}-${pmShowChallengeKeys}`}
                            practice={practice}
                            handlePracticeSelect={handlePracticeSelect}
                            gIdx={gIdx}
                            dIdx={dIdx}
                            headingChars={headingChars}
                            isChallengeFinished={isChallengeFinished}
                            activePractice={activePractice}
                            isRandomMode={isChallengeMode(practice) ? true : isRandomMode}
                            displayLayerIndices={displayLayerIndices}
                            keyboardLayers={keyboardLayers}
                            cols={cols}
                            fixedWidth={fixedWidth}
                            showKeyLabels={isChallengeMode(practice) ? pmShowChallengeKeys : showKeyLabels}
                            lastInvalidKeyCode={lastInvalidKeyCode}
                            lastInvalidLayerIndex={lastInvalidLayerIndex}
                            currentFunctionKeyMap={currentFunctionKeyMap}
                            training={training}
                            typedChar={typedChar}
                            isAlphaNumericMode={false}
                            isInvalidInputTarget={isInvalidInputTargetCallback}
                        />
                    )
                }
                </>
            ) : (
                <>
                {/* {console.log('[App.tsx Render] Training is OFF. Rendering LayoutView.')} */}
                <LayoutView
                    layers={layers}
                    title={title}
                    cols={cols}
                    fixedWidth={fixedWidth}
                    fw={fw}
                    sn={sn}
                    training={training}
                />
                </>
            )}
            </div> {/* End of main content div */}
        </> // Outermost fragment end
    );
}
