// /home/coffee/my-keymap-viewer/src/hooks/useKigoChallengePractice.ts
import { useState, useEffect, useCallback, useRef, useMemo } from 'react'; // useMemo をインポート
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    ChallengeResult, PracticeInputResult, // PracticeInputResult をインポート
    PracticeHighlightResult,
    HighlightInfo,
} from './usePracticeCommons';
import {
    kigoPractice1Data,
    kigoPractice2Data,
    kigoPractice3Data,
    kigoMapping2,
    kigoMapping3,
} from '../data/keymapData';
// import { type } from 'os'; // このインポートは不要そうなので削除検討

// 長押し判定時間 (ミリ秒) - useKigoPractice1.ts からコピー
const LONG_PRESS_DURATION = 300; // 0.3秒に変更

// カウントダウン秒数
const COUNTDOWN_SECONDS = 5;

// 記号チャレンジモードの状態
interface KigoChallengeState {
    targetChar: string | null;
    targetGyouKey: string | null;
    targetLayerIndex: number | null;
    targetType: 'kigo1' | 'kigo2' | 'kigo3' | null; // どのデータセット由来か
    isEqualSign: boolean; // = キーが必要か
    currentStep: number; // 0: 初期状態, 1: 1打鍵目完了
    remainingTime: number;
    status: 'idle' | 'countdown' | 'running' | 'finished' | 'incorrect'; // 'incorrect' を追加
    countdownValue: number;
    startTime: number | null;
    endTime: number | null;
    totalQuestions: number; // 正解シーケンス完了数
    correctAnswers: number; // 正解シーケンス完了数 (totalQuestionsと同じ)
    totalCharsTyped: number; // 入力した総文字数（正誤問わず）
    totalKeyPresses: number; // 総打鍵数
    incorrectKeyPresses: number; // 不正解打鍵数
    currentConsecutiveCorrect: number; // 現在の連続正解数
    maxConsecutiveCorrect: number; // 最大連続正解数
    challengeResults: ChallengeResult | null;
    isLongPressSuccess: boolean;
    showHighlightForEqualSign?: boolean;
    currentInputErrorLayer: number | null;
}

const initialState: KigoChallengeState = {
    targetChar: null, targetGyouKey: null, targetType: null,
    isEqualSign: false, currentStep: 0, remainingTime: 60000, status: 'idle', targetLayerIndex: 6,
    countdownValue: COUNTDOWN_SECONDS, startTime: null, endTime: null, totalQuestions: 0,
    correctAnswers: 0, totalCharsTyped: 0, totalKeyPresses: 0, incorrectKeyPresses: 0,
    currentConsecutiveCorrect: 0, maxConsecutiveCorrect: 0, // 初期状態に連続正解数フィールドを追加
    challengeResults: null, isLongPressSuccess: false, showHighlightForEqualSign: false,
    currentInputErrorLayer: null, // 初期状態にエラーレイヤーフィールドを追加
};


// 記号チャレンジモードのカスタムフック
const useKigoChallengePractice = (props: PracticeHookProps): PracticeHookResult => {
    const { isActive, side, layers, kb } = props; // layers, kb を受け取る

    const [state, setState] = useState<KigoChallengeState>(initialState);
    const stateRef = useRef(state); // 常に最新の state を参照するための ref (useCallback 内で使う)
    const pressInfoRef = useRef<{ code: number; timestamp: number } | null>(null);
    const longPressTimerRef = useRef<number | null>(null); // Timer for Kigo1 long press
    const equalSignHighlightTimerRef = useRef<number | null>(null); // Timer for re-highlighting '='
    const prevIsActiveRef = useRef(isActive); // isActive の前回の値を保持

    // state が更新されるたびに ref も更新
    useEffect(() => {
        stateRef.current = state;
    }, [state]);

    // 組み合わせた全記号リストを作成 (初回のみ)
    const allKigoChars = useRef<
        { char: string; gyouKey: string | null; layerIndex: number; type: 'kigo1' | 'kigo2' | 'kigo3'; isEqualSign: boolean }[] // gyouKey を null 許容に
    >([]).current;

    useEffect(() => {
        if (allKigoChars.length === 0) {
            // kigoPractice1Data からの記号には layerIndex: 6 を設定
            kigoPractice1Data.forEach(group => {
                group.chars.forEach((char, index) => {
                    // kigoPractice1Data の inputDef からキー名を取得する想定だが、
                    // このフックでは gyouKey は直接使わないので null でも良いかもしれない。
                    // ただし、将来的な拡張性を考慮し、inputDef の keyName を使うのが望ましい。
                    // ここでは仮に group.inputs[index].keyName を gyouKey として扱う（要確認）
                    const keyName = group.inputs[index]?.keyName ?? null; // null チェックを追加
                    allKigoChars.push({ char, gyouKey: keyName, layerIndex: 6, type: 'kigo1', isEqualSign: false });
                });
            });
            // kigoPractice2Data からの記号には layerIndex: 7 を設定
            kigoPractice2Data.forEach(group => {
                group.chars.forEach((char: string, index: number) => { // index を追加
                    // group.inputs[index].gyouKey を使う
                    const gyouKey = group.inputs[index]?.gyouKey ?? null; // null チェック
                    allKigoChars.push({ char, gyouKey: gyouKey, layerIndex: 7, type: 'kigo2', isEqualSign: false });
                });
            });
            kigoPractice3Data.forEach(group => {
                group.chars.forEach((char, index) => {
                    const isEqualSign = char === '＝';
                    // '=' の場合は gyouKey は null、それ以外は inputDef から取得
                    const gyouKey = isEqualSign ? null : (group.inputs[index]?.gyouKey ?? null);
                    allKigoChars.push({ char, gyouKey: gyouKey, layerIndex: 8, type: 'kigo3', isEqualSign: isEqualSign });
                });
            });
        }
    }, [allKigoChars]); // allKigoChars は ref なので依存配列は初回のみ実行される
    // 次のターゲットを選択する関数
    const clearLongPressTimer = useCallback(() => {
        if (longPressTimerRef.current !== null) {
            clearTimeout(longPressTimerRef.current);
            longPressTimerRef.current = null;
        }
    }, []);

    const selectNextTarget = useCallback(() => {
        if (allKigoChars.length === 0) {
            console.warn("[KigoChallenge] allKigoChars is empty, cannot select next target.");
            return;
        }

        // ランダムに記号を選択
        const randomIndex = Math.floor(Math.random() * allKigoChars.length);
        const selected = allKigoChars[randomIndex];

        setState((prev: KigoChallengeState) => ({
            ...prev,
            targetChar: selected.char,
            targetGyouKey: selected.gyouKey,
            targetLayerIndex: selected.layerIndex,
            targetType: selected.type,
            isEqualSign: selected.isEqualSign,
            currentStep: 0, // ステップをリセット
            isLongPressSuccess: false,
            showHighlightForEqualSign: false, // 新しいターゲットではハイライト状態もリセット
            currentInputErrorLayer: null, // 新しいターゲットでエラーもリセット
        }));
        pressInfoRef.current = null;
        clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [allKigoChars, clearLongPressTimer]);
    // チャレンジ開始/リセット処理
    const startChallenge = useCallback(() => {
        setState({ // initialState を直接使う
            ...initialState, // スプレッド構文で initialState の全プロパティを展開
            status: 'countdown',
            remainingTime: 60000,
            countdownValue: COUNTDOWN_SECONDS,
            startTime: Date.now(), // 開始時に設定
            endTime: null,
            challengeResults: null,
            isLongPressSuccess: false,
            showHighlightForEqualSign: false, // 開始時にもリセット
            // 連続正解数も初期化
            currentConsecutiveCorrect: 0,
            maxConsecutiveCorrect: 0,
            currentInputErrorLayer: null, // 開始時にエラーもリセット
        });
        pressInfoRef.current = null;
        clearLongPressTimer();
    }, []); // 依存配列は空 (initialState は外にあるため)

    // カウントダウン処理
    useEffect(() => {
        if (isActive && state.status === 'countdown') {
            if (state.countdownValue > 0) {
                const timerId = setTimeout(() => {
                    setState((prev: KigoChallengeState) => ({ ...prev, countdownValue: prev.countdownValue - 1 }));
                }, 1000);
                return () => clearTimeout(timerId);
            } else {
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    status: 'running',
                    remainingTime: 60000,
                    startTime: Date.now(), // 実行開始時に設定
                    currentInputErrorLayer: null, // 開始時にエラーもリセット
                }));
            }
        }
    }, [isActive, state.status, state.countdownValue]); // selectNextTarget は不要

    useEffect(() => {
        if (state.status === 'running' && !state.targetChar) { // ターゲットがまだない場合のみ
            selectNextTarget();
        }
    }, [state.status, selectNextTarget, state.targetChar]); // state.targetChar も依存配列に追加

    // チャレンジ終了処理
    const finishChallenge = useCallback(() => {
        const now = Date.now();
        const { startTime, totalQuestions, correctAnswers, totalCharsTyped, totalKeyPresses, incorrectKeyPresses, maxConsecutiveCorrect } = stateRef.current; // maxConsecutiveCorrect を取得
        const durationSeconds = startTime ? (now - startTime) / 1000 : 0;

        const durationMinutes = durationSeconds / 60;
        const cpmValue = durationMinutes > 0 ? Math.round(correctAnswers / durationMinutes) : 0; // 記号チャレンジでは正解数 = 文字数とする
        const accuracy = totalKeyPresses > 0 ? (totalKeyPresses - incorrectKeyPresses) / totalKeyPresses : 0;

        // missCount を計算
        const missCount = totalKeyPresses - correctAnswers; // ミスタイプ数ではなく、総打鍵数 - 正解シーケンス数とするか、incorrectKeyPresses を使うか検討
        // ここでは incorrectKeyPresses を使用
        const calculatedMissCount = incorrectKeyPresses;


        const results: ChallengeResult = { // ChallengeResults -> ChallengeResult に修正
            totalQuestions, // 達成課題数 = 正解シーケンス完了数
            totalCharsTyped: totalKeyPresses, // 総打鍵数
            correctCharsCount: correctAnswers, // 正解文字数 = 正解シーquence完了数
            correctCount: correctAnswers, // 正解タイプ数 = 正解シーケンス完了数
            missCount: calculatedMissCount, // ミスタイプ数
            accuracy: accuracy, // (総打鍵数 - 不正解打鍵数) / 総打鍵数
            score: 0, // Score will be calculated in PracticeView
            cpm: cpmValue, // Include CPM
            rankMessage: '', // Rank will be calculated in PracticeView
            maxConsecutiveCorrectChars: maxConsecutiveCorrect, // 最大連続正解数を追加
        };

        setState((prev: KigoChallengeState) => ({
            ...prev,
            endTime: now,
            status: 'finished',
            challengeResults: results,
            targetChar: null, // ターゲットをクリア
            targetGyouKey: null,
            targetLayerIndex: null,
            showHighlightForEqualSign: false, // 終了時にもリセット
            currentInputErrorLayer: null, // 終了時にエラーもリセット
        }));
    clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [clearLongPressTimer]);
  
    useEffect(() => {
        let intervalId: NodeJS.Timeout | null = null;
        if (isActive && state.status === 'running') {
            intervalId = setInterval(() => {
                setState((prev: KigoChallengeState) => {
                    const newRemainingTime = Math.max(0, prev.remainingTime - 100);
                    if (newRemainingTime === 0 && prev.status === 'running') { // statusもチェック
                        // finishChallenge を直接呼ぶ代わりに、status を 'finished' にする
                        // finishChallenge のロジックは status が 'finished' になった際の useEffect で処理
                        return { ...prev, remainingTime: 0, status: 'finished' };
                    }
                    return { ...prev, remainingTime: newRemainingTime, showHighlightForEqualSign: prev.showHighlightForEqualSign };
                });
            }, 100);
        } else if (intervalId) {
            clearInterval(intervalId);
        }
        return () => {
            if (intervalId) clearInterval(intervalId);
        };
    }, [isActive, state.status]); // finishChallenge を依存配列から削除

    useEffect(() => {
        if (state.status === 'finished' && !state.endTime) { // endTime がまだ設定されていない場合のみ実行
            finishChallenge();
        }
    }, [state.status, state.endTime, finishChallenge]);

    // 練習モードがアクティブになったらチャレンジ開始
    useEffect(() => {
        // isActive が false になった最初のタイミングでリセット
        if (!isActive && prevIsActiveRef.current) {
            // console.log(`[KigoChallenge useEffect] Resetting state because isActive became false.`);
            setState(initialState); // initialState に戻す
            pressInfoRef.current = null;
            // remainingTime は initialState でリセットされる
            clearLongPressTimer();
            if (equalSignHighlightTimerRef.current) {
                clearTimeout(equalSignHighlightTimerRef.current);
                equalSignHighlightTimerRef.current = null;
            }
        } else if (isActive && state.status === 'idle') { // isActive が true で status が idle の場合のみカウントダウン開始
            startChallenge(); // useCallback でメモ化された関数を呼ぶ
        }
        prevIsActiveRef.current = isActive; // 最後に前回の値を更新
    }, [isActive, state.status, startChallenge, clearLongPressTimer]);

    // 入力処理
    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {

        const { targetChar, targetGyouKey, targetLayerIndex, isEqualSign, currentStep, targetType } = stateRef.current; // targetType も取得
        const { isLongPressSuccess } = stateRef.current;
        const pressCode = info.pressCode;

        // --- Helper function definitions (moved here) ---
        // キー名からキーコード（インデックス+1）を取得するヘルパー関数
        const findKeyCode = (keyName: string | null, layerIdx: number | null): number => {
            if (!keyName) return -1;
            const expectedLayer = layerIdx ?? -1; // Use targetLayerIndex from outer scope
            if (expectedLayer === -1 || !layers[expectedLayer]) return -1;
            
            const layer = layers[expectedLayer];
            // Trim both the search keyName and the keys in the layer for comparison
            const trimmedKeyName = keyName.trim(); 
            for (let i = 0; i < layer.length; i++) {
                if (layer[i] && layer[i].trim() === trimmedKeyName) {
                    return i + 1; // Return 1-based index
                }
            }
            return -1; // 見つからなかった場合
        };
        // kigoPractice1 (レイヤー6) の場合のキーコード取得ロジック
        const getExpectedKeyCodeForKigo1 = (): number => {
            return findKeyCode(targetGyouKey, 6); // kigo1 の targetGyouKey はレイヤー6のキー名
        };
        // kigoPractice2/3 の '記号' キーのコードを取得
        const getKigoKeyCodeForKigo2 = (): number => findKeyCode('記号', 7); // kigo2 の2打目はレイヤー7の'記号'
        const getKigoKeyCodeForKigo3 = (): number => findKeyCode('＝\n記号', 8); // kigo3 の1打目と '=' の2打目はレイヤー8の'＝\n記号'
        // kigoPractice2 の1打目 (レイヤー7の記号キー) のコードを取得
        const getExpectedKeyCodeForKigo2Layer7 = (): number => findKeyCode(targetChar, 7); // kigo2 の1打目はレイヤー7のターゲット記号
        // kigoPractice3 の2打目 (レイヤー8の記号キー) のコードを取得
        const getExpectedKeyCodeForKigo3Layer8 = (): number => {
            // targetChar と isEqualSign は handleInput のスコープから参照
            if (isEqualSign) { // ターゲットが「＝」の場合
                return findKeyCode('＝\n記号', 8); // 2打目も「＝\n記号」キー
            }
            // それ以外の kigo3 の記号の場合
            return findKeyCode(targetChar, 8);
        };
        // --- End of helper function definitions ---

        if (info.type === 'press') {
            const pressCode = info.pressCode; // 0-based
            const { targetType } = stateRef.current;

            if (targetType === 'kigo1') {
                const expectedKeyCode = getExpectedKeyCodeForKigo1(); // 1-based
                if ((pressCode + 1) === expectedKeyCode) { // Compare 0-based+1 with 1-based
                    // 期待されるキーが押されたらタイマー開始
                    pressInfoRef.current = { code: pressCode, timestamp: info.timestamp };
                    clearLongPressTimer(); // 既存のタイマーがあればクリア
                    longPressTimerRef.current = window.setTimeout(() => {
                        if (pressInfoRef.current && pressInfoRef.current.code === pressCode) {
                            setState((prev: KigoChallengeState) => ({ ...prev, isLongPressSuccess: true }));
                        }
                        longPressTimerRef.current = null;
                    }, LONG_PRESS_DURATION);
                }
            }
            return { isExpected: false, shouldGoToNext: false, errorLayer: null }; // Press イベントはここで終了
        }

        if (stateRef.current.status !== 'running' || !targetChar) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        setState((prev: KigoChallengeState) => ({ ...prev, totalKeyPresses: prev.totalKeyPresses + 1 }));

        // ターゲットに必要なキーコードを取得 (リリースイベント用)
        let expectedFirstKeyCode = -1; // 1-based
        let expectedSecondKeyCode = -1; // 1-based

        let isCorrectInput = false; // この入力が正解だったか
        let isSequenceCompletedThisTurn = false; // この入力で記号の入力シーケンスが完了したか
        let nextStep = currentStep;
        let shouldGoToNext = false;
        let errorLayer: number | null = null; // Initialize errorLayer for return

        // 1打鍵目の評価 (= が不要な場合、または = が必要な場合の1打鍵目)
        if (currentStep === 0) {
            if (targetType === 'kigo1') { // レイヤー6 (1打鍵のみ)
                expectedFirstKeyCode = getExpectedKeyCodeForKigo1(); // 1-based
                if (pressInfoRef.current && (pressInfoRef.current.code + 1) === expectedFirstKeyCode) { // Compare 0-based+1 with 1-based
                    const pressTimestamp = pressInfoRef.current.timestamp;
                    const releaseTimestamp = info.timestamp;
                    const duration = releaseTimestamp - pressTimestamp;

                    clearLongPressTimer(); // タイマー解除

                    if (duration >= LONG_PRESS_DURATION && isLongPressSuccess) {
                        isCorrectInput = true;
                        nextStep = 1; // 完了
                        shouldGoToNext = true;
                        isSequenceCompletedThisTurn = true; // 1打鍵で完了
                        errorLayer = null;
                    } else {
                        isCorrectInput = false; // 長押し失敗またはキー違い
                        errorLayer = 6; // レイヤー6でエラー
                        isSequenceCompletedThisTurn = true; // 1打鍵で完了 (失敗)
                    }
                } else {
                    isCorrectInput = false; // 押下情報がない、または押下キーと違う
                    errorLayer = 6; // レイヤー6でエラー
                    isSequenceCompletedThisTurn = true; // 1打鍵で完了 (失敗)
                }
                pressInfoRef.current = null;
                setState((prev: KigoChallengeState) => ({ ...prev, isLongPressSuccess: false }));

            } else if (targetType === 'kigo2') { // レイヤー7 (記号キー -> レイヤー7の記号キー)
                expectedFirstKeyCode = getExpectedKeyCodeForKigo2Layer7(); // 1-based (ターゲット記号そのもの)
                if ((pressCode + 1) === expectedFirstKeyCode) { // Compare 0-based+1 with 1-based
                    isCorrectInput = true; // 1打目は正解
                    nextStep = 1; // 2打鍵目待ち
                    errorLayer = null;
                    isSequenceCompletedThisTurn = false; // まだ完了していない
                } else {
                    isCorrectInput = false; // 1打目が不正解
                    errorLayer = 7; // レイヤー7でエラー
                    isSequenceCompletedThisTurn = true; // 1打鍵で完了 (失敗)
                }
            } else if (targetType === 'kigo3') { // レイヤー8 ('＝\n記号' キー -> レイヤー8の記号キー)
                expectedFirstKeyCode = getKigoKeyCodeForKigo3(); // 1-based ('＝\n記号' キー)

                if (isEqualSign) { // ターゲットが「＝」の場合 (2打鍵必要)
                    if ((pressCode + 1) === expectedFirstKeyCode) { // 1打目は「＝\n記号」キー
                        isCorrectInput = true; // 1打目は正解
                        if (equalSignHighlightTimerRef.current) clearTimeout(equalSignHighlightTimerRef.current);
                        setState(prev => ({ ...prev, showHighlightForEqualSign: false }));
                        equalSignHighlightTimerRef.current = window.setTimeout(() => {
                            if (stateRef.current.targetChar === '＝' && stateRef.current.currentStep === 1) {
                                setState(prev => ({ ...prev, showHighlightForEqualSign: true }));
                            }
                            equalSignHighlightTimerRef.current = null;
                        }, 300); // 0.3秒後に再表示
                        nextStep = 1; // 2打鍵目待ち
                        errorLayer = null;
                        isSequenceCompletedThisTurn = false; // まだ完了していない
                    } else {
                        isCorrectInput = false; // 1打目が不正解
                        errorLayer = 8; // レイヤー8でエラー
                        isSequenceCompletedThisTurn = true; // 1打鍵で完了 (失敗)
                    }
                } else { // ターゲットが「＝」以外の場合 (2打鍵必要: ＝\n記号 -> ターゲット記号)
                    // 1打目は「＝\n記号」キーを期待
                    if ((pressCode + 1) === expectedFirstKeyCode) {
                        isCorrectInput = true; // 1打目は正解
                        nextStep = 1; // 2打鍵目待ち
                        errorLayer = null;
                        isSequenceCompletedThisTurn = false; // まだ完了していない
                    } else {
                        isCorrectInput = false; // 1打目が不正解
                        errorLayer = 8; // レイヤー8でエラー
                        isSequenceCompletedThisTurn = true; // 1打鍵で完了 (失敗)
                    }
                }
            }
        }
        // 2打鍵目の評価 (kigo2, kigo3)
        else if (currentStep === 1) {
            // 2打目待ちの状態では、ターゲットが「＝」かそれ以外かで期待するキーが異なる
            if (targetType === 'kigo2') { // レイヤー7 (記号キー)
                expectedSecondKeyCode = getKigoKeyCodeForKigo2(); // 1-based ('記号' キー)
                if ((pressCode + 1) === expectedSecondKeyCode) { // Compare 0-based+1 with 1-based
                    isCorrectInput = true; // 2打目は正解
                    nextStep = 2; // 完了
                    shouldGoToNext = true;
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了
                    errorLayer = null;
                } else {
                    isCorrectInput = false; // 2打目が不正解
                    errorLayer = 7; // レイヤー7でエラー
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了 (失敗)
                }
            } else if (targetType === 'kigo3' && isEqualSign) { // ターゲットが「＝」の場合の2打目
                expectedSecondKeyCode = getKigoKeyCodeForKigo3(); // 1-based ('＝\n記号' キー)
                 if ((pressCode + 1) === expectedSecondKeyCode) { // Compare 0-based+1 with 1-based
                    isCorrectInput = true; // 2打目は正解
                    nextStep = 2; // 完了
                    shouldGoToNext = true;
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了
                    errorLayer = null;
                } else {
                    isCorrectInput = false; // 2打目が不正解
                    errorLayer = 8; // レイヤー8でエラー
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了 (失敗)
                }
            } else if (targetType === 'kigo3' && !isEqualSign) { // ターゲットが「＝」以外の場合の2打目
                expectedSecondKeyCode = getExpectedKeyCodeForKigo3Layer8(); // 1-based (ターゲット記号そのもの)
                if ((pressCode + 1) === expectedSecondKeyCode) { // Compare 0-based+1 with 1-based
                    isCorrectInput = true; // 2打目は正解
                    nextStep = 2; // 完了
                    shouldGoToNext = true;
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了
                    errorLayer = null;
                } else {
                    isCorrectInput = false; // 2打目が不正解
                    errorLayer = 8; // レイヤー8でエラー
                    isSequenceCompletedThisTurn = true; // 2打鍵で完了 (失敗)
                }
            } else {
                 // currentStep === 1 だが、targetType が kigo2/kigo3 でない、または isEqualSign の判定がおかしい場合
                 // これは予期しない状態なので、不正解としてリセット
                 isCorrectInput = false;
                 isSequenceCompletedThisTurn = true; // シーケンス完了とみなし、リセット
            }
        }


        // 連続正解数の更新
        // 1つの記号入力シーケンスが完了した時点でのみ、その成否に基づいて更新する
        if (isSequenceCompletedThisTurn) {
            if (isCorrectInput) { // 正しく記号が完成した場合
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    currentConsecutiveCorrect: prev.currentConsecutiveCorrect + 1,
                    maxConsecutiveCorrect: Math.max(prev.maxConsecutiveCorrect, prev.currentConsecutiveCorrect + 1),
                }));
            } else { // 記号完成の試みがミスで終わった場合 (isCorrectInput が false)
                // console.log(`[KigoChallenge] Resetting consecutive correct due to miss at sequence completion. missCount: ${stateRef.current.incorrectKeyPresses}, currentTarget: ${targetChar}`);
                 setState((prev: KigoChallengeState) => ({
                    ...prev,
                    currentConsecutiveCorrect: 0, // 不正解でリセット
                }));
            }
        }
        // シーケンスの途中の正しい打鍵や、シーケンスの途中の無関係な打鍵では、連続正解数は変更されない。


        // 正誤判定と状態更新
        if (isCorrectInput) {
            setState((prev: KigoChallengeState) => ({
                ...prev, // Keep previous state
                currentStep: nextStep,
                // 正解の場合のみ文字数を加算（仕様による）
                totalCharsTyped: prev.totalCharsTyped + (targetChar?.length ?? 0),
                incorrectKeyPresses: prev.incorrectKeyPresses, // 正解時はミスタイプ数を増やさない
                currentInputErrorLayer: null, // 正解時はエラーレイヤーをクリア
            }));
            // console.log(`[KigoChallenge handleInput CORRECT] targetChar: ${targetChar}, nextStep: ${nextStep}, shouldGoToNext: ${shouldGoToNext}`);

            if (shouldGoToNext) {
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    totalQuestions: prev.totalQuestions + 1,
                    correctAnswers: prev.correctAnswers + 1,
                }));
                selectNextTarget(); // 次の問題へ
            }
            return { isExpected: true, shouldGoToNext: false, errorLayer: null }; // 正解時は errorLayer を null で返す
        } else {
            // 不正解の場合
            // console.log(`[KigoChallenge handleInput INCORRECT] targetChar: ${targetChar}, currentStep: ${currentStep}, calculated errorLayer: ${errorLayer}`);
            setState((prev: KigoChallengeState) => ({
                ...prev,
                currentStep: 0, // ステップをリセット
                incorrectKeyPresses: prev.incorrectKeyPresses + 1,
                currentInputErrorLayer: errorLayer, // 不正解時はエラーレイヤーをセット
            }));
            // 不正解の場合、ターゲットが「＝」で2打目待ちだった場合にのみハイライトタイマーをクリア
            if (isEqualSign && currentStep === 1) {
                if (equalSignHighlightTimerRef.current) clearTimeout(equalSignHighlightTimerRef.current);
                setState(prev => ({ ...prev, showHighlightForEqualSign: false }));
            }
            return { isExpected: false, shouldGoToNext: false, errorLayer }; // Return the calculated errorLayer for this input
        }
    }, [layers, selectNextTarget, clearLongPressTimer, stateRef]);

    // リセット処理
    const reset = useCallback(() => {
        setState(initialState);
        // startChallenge(); // startChallenge は isActive の useEffect で呼ばれる
        pressInfoRef.current = null;
        clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [clearLongPressTimer]);

    // ヘッダー表示用の文字配列
    const headingChars = useMemo(() => {
        if (state.status === 'countdown') {
            return [`${state.countdownValue}秒`];
        } else if (state.status === 'finished' && state.challengeResults) {
            return ['終了！'];
        } else if (state.status === 'running' && state.targetChar) {
            // 現在のターゲット文字を表示
            return [state.targetChar];
        }
        return ['読み込み中...'];
    }, [state.status, state.countdownValue, state.targetChar, state.challengeResults]);

    // ハイライト処理
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        const { status, targetGyouKey, targetLayerIndex, isEqualSign, currentStep, targetType, targetChar, showHighlightForEqualSign, currentInputErrorLayer } = state; // Use state directly

        if (status !== 'running' || targetLayerIndex === null) {
            return noHighlight;
        }

        // エラーハイライトがアクティブな場合、期待キーのハイライトは表示しない
        // PracticeView 側で isInvalidInputTarget を使ってエラーキーをハイライトする
        if (currentInputErrorLayer !== null && layoutIndex === currentInputErrorLayer) {
             return noHighlight;
        }

        let expectedKeyDisplayName: string | null = null;
        let expectedLayoutIndex: number | null = null;

        // 修正: targetType ごとに分岐
        if (targetType === 'kigo1') { // レイヤー6
            expectedKeyDisplayName = targetGyouKey; // kigo1 の targetGyouKey はレイヤー6のキー名
            expectedLayoutIndex = 6;
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                 return { className: state.isLongPressSuccess ? 'bg-green-200' : 'bg-blue-100', overrideKey: null };
            }
        } else if (targetType === 'kigo2') { // レイヤー7 (記号キー -> レイヤー7の記号キー)
            expectedLayoutIndex = 7;
            if (currentStep === 0) { // 1打目: ターゲット記号キー (レイヤー7)
                expectedKeyDisplayName = state.targetChar;
            } else if (currentStep === 1) { // 2打目: '記号' キー (レイヤー7)
                expectedKeyDisplayName = '記号';
            }
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                 return { className: 'bg-blue-100', overrideKey: null };
            }
        } else if (targetType === 'kigo3') { // レイヤー8 ('＝\n記号' キー -> レイヤー8の記号キー)
            expectedLayoutIndex = 8;
            if (currentStep === 0) { // 1打目: '＝\n記号' キー (レイヤー8)
                expectedKeyDisplayName = '＝\n記号';
            } else if (currentStep === 1) { // 2打目: ターゲット記号キー (レイヤー8)
                // If target is '=', the second press is also '＝\n記号'
                if (isEqualSign) {
                    expectedKeyDisplayName = '＝\n記号';
                } else {
                    expectedKeyDisplayName = state.targetChar;
                }
            }
            // 「=」の2打目待ちで、かつハイライトが一時的に消えている場合はハイライトしない
            if (isEqualSign && currentStep === 1 && !showHighlightForEqualSign) {
                 return noHighlight;
            }
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                 return { className: 'bg-blue-100', overrideKey: null };
            }
        }

        return noHighlight;
    }, [
        state.status,
        state.targetLayerIndex,
        state.currentStep,
        state.targetGyouKey,
        state.targetType,
        state.isEqualSign,
        state.isLongPressSuccess,
        state.targetChar,
        state.showHighlightForEqualSign,
        state.currentInputErrorLayer, // Add currentInputErrorLayer to dependencies
    ]);

    // 不正入力ターゲット判定
    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        // stateRef から最新の値を取得
        const { status, targetLayerIndex, currentInputErrorLayer } = stateRef.current; // Use currentInputErrorLayer

        // チャレンジモードが実行中でない、またはターゲットレイヤーが設定されていない場合は不正入力判定しない
        if (status !== 'running' || targetLayerIndex === null) {
            return false;
        }

        // エラーが発生していて、かつ、現在レンダリング中のキーが、エラーとなったキーと同じレイヤー、同じインデックスである場合に true を返す
        // Use currentInputErrorLayer to determine if an error highlight is needed for this key.
        // The `pressCode` argument from App.tsx/PracticeView should be the pressCode of the key that caused the error.
        // The `layoutIndex` and `keyIndex` arguments are for the key currently being rendered.
        // We need to check if the rendered key is the one that caused the error.
        // Assuming App.tsx/PracticeView calls this with the error details:
        // isInvalidInputTarget(errorHighlightDetails.pressCode, layoutIndex, keyIndex)
        if (currentInputErrorLayer !== null && layoutIndex === currentInputErrorLayer && keyIndex === pressCode) {
             return true;
        }
        return false;
    }, [state.status, state.targetLayerIndex]); // state.status, state.targetLayerIndex を依存配列に追加

    // getHighlight (ダミー実装)
    const getHighlight = useCallback((): HighlightInfo => {
        return { start: null, end: null };
    }, []);

    const clearErrorState = useCallback(() => {
        setState(prev => ({ ...prev, currentInputErrorLayer: null }));
    }, [setState]);

    const displayLayers = useMemo(() => {
        const { targetType, targetLayerIndex } = state;

        // App.tsx が targetLayerIndex を見て適切なレイヤーを表示する

        return layers;
    }, [state.targetType, state.targetLayerIndex, layers]); // layers も依存配列に追加

    return {
        // ... (他のプロパティはそのまま) ...
        handleInput,
        reset,
        headingChars,
        getHighlightClassName, // highlightedKeys -> getHighlightClassName
        isInvalidInputTarget,
        getHighlight, // getHighlight を追加
        targetLayerIndex: state.targetLayerIndex,
        displayLayers,
        challengeResults: state.challengeResults,
        status: state.status,
        countdownValue: state.countdownValue,
        targetChar: state.targetChar ?? undefined, // targetChar を追加
        challengeTimer: state.remainingTime,
        challengeTotalTime: 60000,
        errorHighlightLayer: state.currentInputErrorLayer,
        clearErrorState,
    };
};

export default useKigoChallengePractice;
